import * as React from 'react';
import * as ReactDOM from 'react-dom';
import thunk from 'redux-thunk';
import {compose, createStore, applyMiddleware} from 'redux';
import {Provider} from 'react-redux';
import {notify} from 'react-notify-toast';
import {AppRoute} from './const';
import history from './history';
import App from './components/app/app';
import {createAPI} from './api';
import {Operation as DataOperation} from './reducer/data/data';
import {ActionCreator, AuthorizationStatus, Operation as UserOperation} from './reducer/user/user';
import reducer from './reducer/reducer';


const onError = (err) => {
  notify.show(`  : ${err}`, `error`, 5000, {});
};

const onUnauthorized = ({method, url}) => {
  store.dispatch(ActionCreator.requireAuthorization(AuthorizationStatus.NO_AUTH));
  if (method !== `get` && url !== `/login`) {
    history.push(AppRoute.SIGN_IN);
  }
};

const api = createAPI(onError, onUnauthorized);

const store = createStore(
    reducer,
    compose(
        applyMiddleware(thunk.withExtraArgument(api)),
        // window[`__REDUX_DEVTOOLS_EXTENSION__`] ? window[`__REDUX_DEVTOOLS_EXTENSION__()`] : (f) => f
    )
);

store.dispatch(DataOperation.loadPlaces());
store.dispatch(UserOperation.checkAuth());

ReactDOM.render(
    <Provider store={store}>
      <App />
    </Provider>,
    document.querySelector(`#root`)
);
