import * as React from 'react';
import {SortType} from '../../const';

interface Props {
  onSortClick: (sortType: string) => void;
  isHidden: boolean;
  sortType: string;
  onMenuClick: () => void;
  listRef: () => {} | { current: React.RefObject<HTMLUListElement> };
}

const SortMenu: React.FunctionComponent<Props> = ({
  onSortClick,
  isHidden,
  sortType,
  onMenuClick,
  listRef
}: Props) => {

  return (
    <form className="places__sorting" action="#" method="get">
      <span className="places__sorting-caption">Sort by </span>
      <span
        className="places__sorting-type"
        tabIndex={0}
        onClick={onMenuClick}
      >
        {sortType}
        <svg className="places__sorting-arrow" width={7} height={4}>
          <use xlinkHref="#icon-arrow-select" />
        </svg>
      </span>
      <ul
        className={`places__options places__options--custom ${isHidden ? `` : `places__options--opened`}`}
        ref={listRef}
      >
        {Object.values(SortType).map((sort: string, index) => {
          return (
            <li
              key={sort + index}
              className="places__option"
              tabIndex={0}
              onClick={() => {
                onSortClick(sort);
              }}
            >
              {sort}
            </li>
          );
        })}
      </ul>
    </form>
  );
};

export default SortMenu;
