import * as React from 'react';
import {Subtract} from 'utility-types';
import {ReviewsForm} from '../../const';

interface InjectingProps {
  id: number;
  isLoading: boolean;
  postReview: ({}) => void;
}

interface State {
  id: number;
  comment: string;
  rating: number;
  isDisabled: boolean;
}

const withReviewForm = (Component) => {

  type P = React.ComponentProps<typeof Component>;
  type T = Subtract<P, InjectingProps>;

  class WithReviewForm extends React.PureComponent<T, State> {
    constructor(props) {
      super(props);

      const {id} = this.props;

      this.state = {
        id,
        comment: ``,
        rating: 0,
        isDisabled: true,
      };

      this._handleSuccessfulSubmit = this._handleSuccessfulSubmit.bind(this);
      this._handleFormSubmit = this._handleFormSubmit.bind(this);
      this._handleRatingChange = this._handleRatingChange.bind(this);
      this._handleCommentChange = this._handleCommentChange.bind(this);
    }

    componentDidUpdate(prevProps, prevState) {
      if (prevState.comment !== this.state.comment || prevState.rating !== this.state.rating) {
        this.setState((state) => ({
          isDisabled: state.comment.length < ReviewsForm.MIN_COMMENT_LENGTH || state.rating === 0,
        }));
      }
    }

    _handleSuccessfulSubmit() {
      this.setState({
        comment: ``,
        rating: 0,
      });
    }

    _handleFormSubmit(evt) {
      evt.preventDefault();

      const {postReview} = this.props;
      const {comment, rating, id} = this.state;

      postReview({comment, rating, id}, this._handleSuccessfulSubmit);
    }

    _handleRatingChange(evt) {
      this.setState({
        rating: Number(evt.target.value),
      });
    }

    _handleCommentChange(evt) {
      this.setState({
        comment: evt.target.value,
      });
    }

    render() {
      const {isDisabled, comment, rating} = this.state;
      const {isLoading} = this.props;

      return (
        <Component
          {...this.props}
          comment={comment}
          rating={rating}
          isDisabled={isDisabled}
          isLoading={isLoading}
          onFormSubmit={this._handleFormSubmit}
          onRatingChange={this._handleRatingChange}
          onCommentChange={this._handleCommentChange}
        />
      );
    }
  }

  return WithReviewForm;
};

export default withReviewForm;
