import * as React from 'react';
import withSortMenu from '../../hocs/with-sort-menu/with-sort-menu';
import PlacesList from '../places-list/places-list';
import SortMenu from '../sort-menu/sort-menu';
import Map from '../map/map';
import {PlaceCardType} from '../../const';
import {Place, City} from '../../types';

interface Props {
  places: Place[];
  activeCity: City;
}

const SortMenuWrapped = withSortMenu(SortMenu);

const Places: React.FunctionComponent<Props> = ({places, activeCity}: Props) => {

  return (
    <div className="cities">
      <div className="cities__places-container container">
        <section className="cities__places places">
          <h2 className="visually-hidden">Places</h2>
          <b className="places__found">{places.length} {places.length === 1 ? `place` : `places`} to stay in {activeCity.name}</b>
          <SortMenuWrapped />
          <div className="cities__places-list places__list tabs__content">
            <PlacesList
              type={PlaceCardType.PLACE}
              places={places}
            />
          </div>
        </section>
        <div className="cities__right-section">
          <section className="cities__map map">
            <Map
              places={places}
              currentPlace={null}
            />
          </section>
        </div>
      </div>
    </div>
  );
};

export default Places;
