import * as React from 'react';
import {connect} from 'react-redux';
import CitiesList from '../cities-list/cities-list';
import Places from '../places/places';
import NoPlaces from '../no-places/no-places';
import Header from '../../components/header/header';
import {Place, User, City} from '../../types';
import {getPlacesBySortType, getCities} from '../../reducer/data/selectors';
import {getActiveCity} from '../../reducer/app/selectors';
import {ActionCreator} from '../../reducer/app/app';
import {getUser} from '../../reducer/user/selectors';

interface Props {
  places: Place[];
  cities: City[];
  activeCity: City;
  user: User;
  onCityTabClick: (city: City) => void;
}

const Main: React.FunctionComponent<Props> = ({
  places,
  cities,
  activeCity,
  user,
  onCityTabClick,
}: Props) => {

  return (
    <div className="page page--gray page--main">
      <Header user={user}/>
      <main className={`page__main page__main--index ${places.length ? `` : `page__main--index-empty`}`}>
        <h1 className="visually-hidden">Cities</h1>
        <div className="tabs">
          <section className="locations container">
            <CitiesList
              cities={cities}
              activeCity={activeCity}
              onActiveItemSet={onCityTabClick}
            />
          </section>
        </div>
        {places.length ?
          <Places
            places={places}
            activeCity={activeCity}
          /> :
          <NoPlaces activeCity={activeCity}/>}
      </main>
    </div>
  );
};

const mapStateToProps = (state) => ({
  places: getPlacesBySortType(state),
  cities: getCities(state),
  activeCity: getActiveCity(state),
  user: getUser(state),
});

const mapDispatchToProps = (dispatch) => ({
  onCityTabClick(city) {
    dispatch(ActionCreator.setCity(city));
  },
});

export {Main};
export default connect(mapStateToProps, mapDispatchToProps)(Main);
