import * as React from 'react';
import PlaceCard from '../place-card/place-card';
import {Place, CardType} from '../../types';

interface Props {
  places: Place[];
  type: CardType;
}

const PlacesList: React.FunctionComponent<Props> = ({places, type}: Props) => {
  return (
    <React.Fragment>
      {places.map((place) => {
        return (
          <PlaceCard
            type={type}
            key={place.id}
            place={place}
          />
        );
      })}
    </React.Fragment>
  );
};

export default PlacesList;
